.profile-container {
  background: linear-gradient(135deg, #0a1446 0%, #1a237e 60%, #3f51b5 100%);
  font-family: 'Segoe UI', 'PingFang SC', 'Hiragino Sans GB', 'Arial', sans-serif;
  padding: 24px 0 40px 0;
  min-height: 100vh;
}

/* 板块通用样式 */
.profile-section {
  max-width: 800px;
  margin: 0 auto 32px auto;
  padding: 0 20px;
}

.section-header {
  display: flex;
  align-items: center;
  margin-bottom: 24px;
  padding: 0 8px;
}

.section-icon {
  font-size: 2rem;
  margin-right: 16px;
  background: rgba(66, 165, 245, 0.2);
  width: 60px;
  height: 60px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 2px solid #42a5f5;
}

.section-title {
  font-size: 1.8rem;
  font-weight: bold;
  color: #42a5f5;
  margin: 0;
}

/* 个人信息板块 */
.profile-card {
  background: rgba(30, 40, 90, 0.7);
  border-radius: 20px;
  box-shadow: 0 8px 32px rgba(10,20,70,0.18);
  padding: 36px 32px 28px 32px;
  color: #fff;
}

.profile-header {
  display: flex;
  align-items: center;
  margin-bottom: 24px;
}

.profile-avatar-placeholder {
  width: 120px;
  height: 120px;
  border-radius: 50%;
  background: rgba(66, 165, 245, 0.2);
  border: 4px solid #42a5f5;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 2px 8px rgba(66,165,245,0.18);
}

.profile-avatar-text {
  color: #42a5f5;
  font-size: 1.2rem;
  font-weight: 500;
}

.profile-info {
  margin-left: 32px;
}

.profile-title {
  font-size: 2rem;
  font-weight: bold;
  color: #42a5f5;
  margin: 0 0 8px 0;
}

.profile-role {
  color: #b6cfff;
  font-size: 1.2rem;
  margin: 0 0 4px 0;
}

.profile-company {
  color: #9ca3af;
  font-size: 1rem;
  margin: 0;
}

.profile-desc {
  color: #e3eafc;
  font-size: 1.15rem;
  line-height: 1.6;
  margin-bottom: 32px;
}

.profile-stats {
  display: flex;
  justify-content: space-around;
  gap: 20px;
}

.profile-stat {
  text-align: center;
  flex: 1;
}

.profile-stat-num {
  font-size: 1.8rem;
  font-weight: bold;
  color: #42a5f5;
  margin: 0 0 8px 0;
}

.profile-stat-label {
  font-size: 1rem;
  color: #b6cfff;
  margin: 0;
}

/* 岗位职责板块 */
.responsibilities-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 20px;
}

.responsibility-card {
  background: rgba(30, 40, 90, 0.7);
  border-radius: 16px;
  padding: 24px;
  color: #fff;
  border: 1px solid rgba(66, 165, 245, 0.3);
  transition: all 0.3s ease;
}

.responsibility-card:hover {
  transform: translateY(-4px);
  box-shadow: 0 8px 24px rgba(66, 165, 245, 0.2);
  border-color: #42a5f5;
}

.responsibility-icon {
  font-size: 2.5rem;
  margin-bottom: 16px;
}

.responsibility-title {
  font-size: 1.3rem;
  font-weight: 600;
  color: #42a5f5;
  margin: 0 0 12px 0;
}

.responsibility-desc {
  color: #e3eafc;
  font-size: 1rem;
  line-height: 1.5;
  margin: 0;
}

/* 岗位职责模块新样式 */
.responsibility-desc-block {
  background: rgba(30, 40, 90, 0.7);
  border-radius: 16px;
  padding: 28px 24px 18px 24px;
  margin-bottom: 24px;
  color: #fff;
  box-shadow: 0 4px 16px rgba(66,165,245,0.08);
}

.responsibility-subtitle {
  font-size: 1.2rem;
  font-weight: 600;
  color: #42a5f5;
  margin-bottom: 16px;
}

.responsibility-desc-cn,
.responsibility-desc-en {
  font-size: 1.05rem;
  line-height: 1.7;
  margin-bottom: 10px;
  color: #e3eafc;
}

.responsibility-desc-en {
  color: #b6cfff;
  font-size: 0.98rem;
}

.responsibility-skills-block {
  background: rgba(30, 40, 90, 0.7);
  border-radius: 16px;
  padding: 28px 24px 18px 24px;
  color: #fff;
  box-shadow: 0 4px 16px rgba(66,165,245,0.08);
}

.skills-list {
  display: flex;
  flex-wrap: wrap;
  gap: 14px 18px;
  margin: 0;
  padding: 0;
  list-style: none;
}

.skills-list li {
  background: rgba(66, 165, 245, 0.15);
  color: #42a5f5;
  border-radius: 12px;
  padding: 8px 18px;
  font-size: 1rem;
  font-weight: 500;
  box-shadow: 0 2px 8px rgba(66,165,245,0.06);
  margin: 0;
  transition: background 0.2s;
}

.skills-list li:hover {
  background: #42a5f5;
  color: #fff;
}

.skills-group {
  margin-bottom: 18px;
}

.skills-group-title {
  font-size: 1.08rem;
  font-weight: 600;
  color: #8ec6ff;
  margin-bottom: 10px;
  letter-spacing: 1px;
}

/* 培训体系板块 */
.training-timeline {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.training-block {
  background: rgba(30, 40, 90, 0.7);
  border-radius: 16px;
  padding: 28px 24px 18px 24px;
  margin-bottom: 24px;
  color: #fff;
  box-shadow: 0 4px 16px rgba(66,165,245,0.08);
}

.training-subtitle {
  font-size: 1.2rem;
  font-weight: 600;
  color: #42a5f5;
  margin-bottom: 16px;
}

.training-group {
  margin-bottom: 18px;
}

.training-group-title {
  font-size: 1.08rem;
  font-weight: 600;
  color: #8ec6ff;
  margin-bottom: 10px;
  letter-spacing: 1px;
}

.training-list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  gap: 12px 18px;
}

.training-item {
  background: rgba(66, 165, 245, 0.15);
  color: #42a5f5;
  border-radius: 12px;
  padding: 8px 18px;
  font-size: 1rem;
  font-weight: 500;
  box-shadow: 0 2px 8px rgba(66,165,245,0.06);
  margin: 0;
  transition: background 0.2s, color 0.2s;
  display: flex;
  align-items: center;
}

.training-item.pending {
  background: rgba(255, 193, 7, 0.15);
  color: #ffc107;
  border: 1px dashed #ffc107;
}

.training-item.completed {
  background: rgba(34, 197, 94, 0.15);
  color: #22c55e;
  border: 1px solid #22c55e;
}

.training-score {
  margin-left: 10px;
  font-size: 0.98rem;
  font-weight: 600;
  color: #fff;
  background: #42a5f5;
  border-radius: 8px;
  padding: 2px 10px;
  box-shadow: 0 1px 4px rgba(66,165,245,0.10);
}

.training-date {
  background: #42a5f5;
  color: #fff;
  padding: 8px 16px;
  border-radius: 20px;
  font-weight: 600;
  font-size: 0.9rem;
  min-width: 60px;
  text-align: center;
}

.training-content {
  flex: 1;
}

.training-title {
  font-size: 1.2rem;
  font-weight: 600;
  color: #fff;
  margin: 0 0 8px 0;
}

.training-org {
  color: #b6cfff;
  font-size: 1rem;
  margin: 0 0 16px 0;
}

.training-progress {
  display: flex;
  align-items: center;
  gap: 12px;
}

.progress-bar {
  flex: 1;
  height: 8px;
  background: rgba(66, 165, 245, 0.2);
  border-radius: 4px;
  overflow: hidden;
}

.progress-fill {
  height: 100%;
  background: #42a5f5;
  border-radius: 4px;
  transition: width 0.3s ease;
}

.progress-text {
  color: #b6cfff;
  font-size: 0.9rem;
  min-width: 60px;
}

/* 职业发展路径板块 */
.career-path {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.career-step {
  background: rgba(30, 40, 90, 0.7);
  border-radius: 16px;
  padding: 24px;
  color: #fff;
  display: flex;
  align-items: center;
  gap: 20px;
  border: 1px solid rgba(66, 165, 245, 0.3);
  transition: all 0.3s ease;
}

.career-step.completed {
  border-color: #22c55e;
  background: rgba(34, 197, 94, 0.1);
}

.career-step.active {
  border-color: #42a5f5;
  background: rgba(66, 165, 245, 0.1);
  box-shadow: 0 0 20px rgba(66, 165, 245, 0.3);
}

.career-step:hover {
  transform: translateX(8px);
}

.step-icon {
  font-size: 2rem;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(66, 165, 245, 0.2);
  border: 2px solid #42a5f5;
}

.career-step.completed .step-icon {
  background: rgba(34, 197, 94, 0.2);
  border-color: #22c55e;
}

.step-content {
  flex: 1;
}

.step-title {
  font-size: 1.3rem;
  font-weight: 600;
  color: #fff;
  margin: 0 0 8px 0;
}

.step-desc {
  color: #e3eafc;
  font-size: 1rem;
  line-height: 1.5;
  margin: 0 0 8px 0;
}

.step-status {
  display: inline-block;
  padding: 4px 12px;
  border-radius: 12px;
  font-size: 0.8rem;
  font-weight: 500;
  background: rgba(66, 165, 245, 0.2);
  color: #42a5f5;
}

.career-step.completed .step-status {
  background: rgba(34, 197, 94, 0.2);
  color: #22c55e;
}

.career-block {
  background: rgba(30, 40, 90, 0.7);
  border-radius: 16px;
  padding: 28px 24px 18px 24px;
  color: #fff;
  box-shadow: 0 4px 16px rgba(66,165,245,0.08);
}

.career-direction {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 18px;
  font-size: 1.15rem;
  font-weight: 500;
  margin-bottom: 22px;
}
.career-current b, .career-next b {
  color: #42a5f5;
}
.career-arrow {
  font-size: 1.5rem;
  color: #8ec6ff;
}

.career-need-group {
  margin-bottom: 18px;
}
.career-need-title {
  font-size: 1.08rem;
  font-weight: 600;
  color: #8ec6ff;
  margin-bottom: 10px;
  letter-spacing: 1px;
}
.career-need-list {
  display: flex;
  flex-wrap: wrap;
  gap: 14px 18px;
  margin: 0;
  padding: 0;
  list-style: none;
}
.career-need-list li {
  background: rgba(66, 165, 245, 0.15);
  color: #42a5f5;
  border-radius: 12px;
  padding: 8px 18px;
  font-size: 1rem;
  font-weight: 500;
  box-shadow: 0 2px 8px rgba(66,165,245,0.06);
  margin: 0;
  transition: background 0.2s;
}
.career-need-list li:hover {
  background: #42a5f5;
  color: #fff;
}

.profile-contact-block {
  margin-top: 28px;
  background: rgba(66, 165, 245, 0.10);
  border-radius: 14px;
  padding: 18px 20px;
  display: flex;
  flex-direction: column;
  gap: 12px;
}
.profile-contact-item {
  display: flex;
  align-items: center;
  font-size: 1.08rem;
}
.profile-contact-label {
  color: #8ec6ff;
  font-weight: 600;
  margin-right: 10px;
}
.profile-contact-value {
  color: #fff;
  font-weight: 500;
  word-break: break-all;
}

/* 响应式设计 */
@media (max-width: 768px) {
  .profile-section {
    padding: 0 16px;
  }
  
  .section-header {
    flex-direction: column;
    text-align: center;
    gap: 12px;
  }
  
  .section-icon {
    margin-right: 0;
  }
  
  .profile-header {
    flex-direction: column;
    text-align: center;
    gap: 16px;
  }
  
  .profile-info {
    margin-left: 0;
  }
  
  .profile-stats {
    flex-direction: column;
    gap: 16px;
  }
  
  .responsibilities-grid {
    grid-template-columns: 1fr;
  }
  
  .training-item {
    flex-direction: column;
    text-align: center;
  }
  
  .career-step {
    flex-direction: column;
    text-align: center;
  }
  
  .step-icon {
    margin-bottom: 12px;
  }
} 